  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 10px;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  border: 2px dashed rgba(255, 255, 255, 0.5);
  border-radius: .4rem;
}
